function get(obj, path, deflt) {
    if (obj) {
        if (path.length === 0) {
            return deflt;
        }
        else if (path.length === 1) {
            if (obj[path[0]]) {
                return obj[path[0]];
            }
            else {
                return deflt;
            }
        }
        else {
            return get(obj[path[0]], path.slice(1), deflt);
        }
    }
    else {
        return deflt;
    }
}
function load(storage, key) {
    const value = storage.getItem(key);
    if (value) {
        return JSON.parse(value);
    }
    else {
        return null;
    }
}
function getConfig(storage, key, path, deflt) {
    const store = load(storage, key);
    return get(store, path.split('.'), deflt);
}
export function getLocalConfig(key, path, deflt) {
    return getConfig(localStorage, key, path, deflt);
}
function set(obj, path, value) {
    if (path.length === 0) {
    }
    else if (path.length === 1) {
        obj[path[0]] = value;
    }
    else {
        if (obj[path[0]]) {
            set(obj[path[0]], path.slice(1), value);
        }
        else {
            const subObj = {};
            set(subObj, path.slice(1), value);
            obj[path[0]] = subObj;
        }
    }
}
function save(storage, key, value) {
    storage.setItem(key, JSON.stringify(value));
}
function setConfig(storage, key, path, value) {
    let store = load(storage, key);
    if (store === null) {
        store = {};
    }
    set(store, path.split('.'), value);
    save(storage, key, store);
}
export function setLocalConfig(key, path, value) {
    setConfig(localStorage, key, path, value);
}
